<template>
  <component :is="component" v-bind="componentData" class="page-wrapper">
    {{ menuLabel }}
    <button @click="loremIpsum">Lorem ipsum</button>
    <div v-html="dummyContent"></div>
  </component>
</template>

<script>
import IndexPage from './pages/IndexPage';
import ArticlePage from './pages/ArticlePage';

export default {
  name: "PageWrapper",
  components: { IndexPage, ArticlePage },
  data() {
    return {
      slug: this.$route.params.slug,
      dummyContent: 'Empty Page'
    };
  },
  props: {
    backgroundColor: String,
    menuLabel: String,
    component: String,
    componentData: { type: Object, default: () => ({}) }
  },
  mounted() {
    console.log(this.$route.params)
  },
  methods: {
    async loremIpsum() {
      this.dummyContent = await fetch('https://baconipsum.com/api/?type=meat-and-filler&paras=20&format=html').then(r => r.text());
    } 
  }
};
</script>

<style>
.page-wrapper {
  overflow-x: hidden;
  overflow-y: auto;
  position: relative;
  height: 100vh;
  transition: all .2s ease-in-out;
  padding: 1em;
}
</style>